export default {
	data() {
		return {
			item: {},
			show: false,
			more: false,
			pageNum: 1,
			pageSize: 20,
			loading: true,
			list: []
		};
	},
	onShareAppMessage() {
		let token = uni.getStorageSync('TOKEN');
		let user = uni.getStorageSync('userInfo');
		if (token) {
			return {
				title: '易建联共享服务平台',
				path: '/pages/index/index?server=' + user.server_people_id
			};
		}
	},
	onLoad(opt) {
		this.getList();
		if (opt.name) {
			this.show = true;
			if (opt.coupon) {
				this.item = JSON.parse(opt.coupon);
			}
			uni.setNavigationBarTitle({
				title: opt.name
			});
		}
	},
	onReachBottom() {
		this.getList();
	},
	methods: {
		doCheck(item) {
			this.item = item;
		},
		doChoose() {
			uni.$emit('choose', this.item);
			uni.navigateBack({});
		},
		getList() {
			if (this.loading) {
				this.loading = false;
				this.$Fetch({
					url: 'Coupon/user_coupon',
					data: { pageNum: this.pageNum, pageSize: this.pageSize },
					success: res => {
						if (res.length > 0) {
							this.list = this.list.concat(res);
							if (res.length === this.pageSize) {
								this.pageNum++;
								this.loading = true;
								this.more = true;
							} else {
								this.more = false;
							}
						} else {
							this.more = false;
						}
					}
				});
			}
		}
	}
};
